
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mapGetters} from 'vuex'
import SelectItem from '@/components/SelectItem'
import {basic, additional, forced} from '../config'
export default {
  data () {
    return {
      forcedConfig: forced,
      basicConfig: basic,
      additionalConfig: additional,
      forcedInsurance: '', // 强制保险
      lossInsurance: 0,
      force: {
        select: 1
      },
      basic: {
        lossInsurance: {
          select: 0, // 车辆损失险
          regardless: 1 // 不计免赔
        },
        thirdParty: {
          select: 0, // 第三者责任险
          value: '50万',
          regardless: 1 // 不计免赔
        },
        driverSeat: {
          select: 0, // 司机座位险
          value: '6万/座',
          regardless: 1 // 不计免赔
        },
        passengerSeat: {
          select: 0, // 乘客座位险
          value: '1万/座',
          regardless: 1 // 不计免赔
        },
        robbery: {
          select: 0 // 盗抢险
        }
      },
      additional: {
        glassCrushing: {
          select: 0, // 玻璃破碎险
          value: '国产',
          regardless: 1
        },
        autoignition: {
          select: 0, // 自燃损失险
          regardless: 1
        },
        wading: {
          select: 0, // 涉水险
          value: '1万/座',
          regardless: 1
        },
        escape: {
          select: 0, // 三者逃逸险
          regardless: 1
        },
        appointedSpecialist: {
          select: 0, // 指定专修厂特约险
          regardless: 1
        }
      }
    }
  },
  components: {
    Group,
    Cell,
    XButton,
    SelectItem
  },
  created () {
    console.log(this.$route.params.id)
  },
  computed: {
    ...mapGetters({
      order: 'getOrder'
    })
  },
  methods: {
    handleSubmit () {
      this.$router.push('/offer/success/' + this.$route.params.id)
    }
  }
}



